<template>
    <div class="pa-3">
        <v-data-table
                :headers="headers"
                :items="garbage"
                :items-per-page="5"
                class="elevation-1"
        >
            <template v-slot:item.img="{item}">
                <p class="img-url">{{item.img}}</p>
            </template>
            <template v-slot:top>
                <v-toolbar flat color="white">
                    <v-toolbar-title>Admin Dashboard</v-toolbar-title>
                    <v-divider
                            class="mx-4"
                            inset
                            vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                        <template v-slot:activator="{ on }">
                            <v-btn color="primary" dark class="mb-2" v-on="on">Add</v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <span class="headline">{{formTitle}}</span>
                            </v-card-title>
                            <v-card-text>
                                <v-container>
                                    <v-row v-for="(prop, index) in Object.keys(editedItem)" :key="index">
                                        <v-col cols="12">
                                            <v-checkbox v-model="editedItem[prop]" :label="headers[index].text" v-if="prop === 'is_verified'"></v-checkbox>
                                            <v-text-field v-model="editedItem[prop]" :label="headers[index].text" v-else-if="prop !== 'id'"></v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-toolbar>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-icon
                        small
                        class="mr-2"
                        @click="editItem(item)"
                >
                    mdi-pencil
                </v-icon>
                <v-icon
                        small
                        @click="deleteItem(item)"
                >
                    mdi-delete
                </v-icon>
            </template>
            <template v-slot:item.points="{ item }">
                <router-link :to="$route.path + '/garbage/' + item.id + '/points'">Points</router-link>
            </template>
        </v-data-table>
        <v-snackbar
                v-model="snackbar"
                :color="color"
        >
            {{ text }}
            <v-btn
                    text
                    @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                itemId: 0,
                headers: [],
                garbage: [],
                dialog: false,
                editedItem: {},
                defaultItem: {},
                editedIndex: -1,
                snackbar: false,
                text: '',
                color: ''
            }
        },
        computed: {
            formTitle() {
                return (this.editedIndex === -1 ? 'New ' : 'Edit ') + (this.$route.name === 'admin_garbage' ? 'garbage' : 'point');
            },
            getUrl() {
                return this.$route.name === 'admin_garbage' ?
                    'https://save-nature-uzh.000webhostapp.com/garbage?headers'
                    :
                    'https://save-nature-uzh.000webhostapp.com/garbage/' + this.$route.params.id + '/points?headers';
            },
            addUrl() {
                return this.$route.name === 'admin_garbage' ?
                    'https://save-nature-uzh.000webhostapp.com/garbage/add'
                    :
                    'https://save-nature-uzh.000webhostapp.com/points/add';
            },
            changeUrl() {
                return this.$route.name === 'admin_garbage' ?
                    'https://save-nature-uzh.000webhostapp.com/garbage/' + this.itemId + '/'
                    :
                    'https://save-nature-uzh.000webhostapp.com/points/' + this.itemId +  '/';
            }
        },
        mounted() {
            fetch(this.getUrl)
                .then(res => res.json())
                .then(json => {
                    const headers = [];
                    const width = 30 / (json.headers.length);
                    json.headers.forEach(h => {
                        const header = {
                            text: h.charAt(0).toUpperCase() + h.slice(1),
                            value: h,
                            width: width + '%'
                        };
                        if (h === 'description') {
                            header.width = '40%'
                        } else if (h === 'img') {
                            header.width = '30%';
                        }
                        headers.push(header);
                        this.$set(this.editedItem, h, null);
                        this.$set(this.defaultItem, h, null);
                    });
                    if (this.$route.name === 'admin_garbage') {
                        headers.push({text: 'Points', value: 'points', sortable: false, width: width + '%'})
                    }
                    this.headers = [...headers, {
                        text: 'Actions',
                        value: 'actions',
                        sortable: false,
                        width: width + '%'
                    }];
                    console.log(this.headers)
                    this.garbage = json.items;
                })
        },
        methods: {
            editItem(item) {
                this.editedIndex = this.garbage.indexOf(item);
                this.itemId = item.id;
                this.editedItem = Object.assign({}, item);
                this.dialog = true
            },

            deleteItem(item) {
                const index = this.garbage.indexOf(item);
                this.itemId = item.id;
                confirm('Are you sure you want to delete this item?') && fetch(this.changeUrl + 'delete', {
                    method: 'DELETE'
                })
                    .then(res => res.json())
                    .then(json => {
                        if (json.success) {
                            this.garbage.splice(index, 1)
                        }
                    })
            },

            close() {
                this.dialog = false;
                setTimeout(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                }, 300)
            },

            save() {
                if (this.editedIndex > -1) {
                    fetch(this.changeUrl + 'update', {
                        method: 'PUT',
                        body: JSON.stringify(this.editedItem)
                    })
                        .then(res => res.json())
                        .then(json => {
                            Object.assign(this.garbage[this.editedIndex], this.editedItem);
                            this.text = json.message;
                            this.color = json.success ? 'success' : 'error';
                            this.snackbar = true;
                            this.close();
                        })
                } else {
                    let body = {};
                    if(this.$route.name === 'admin_garbage_points') {
                        body.point = [];
                        body.garbage = [this.$route.params.id];
                        for (let prop in this.editedItem) {
                            if (prop === 'latitude') {
                                body.point[0] = this.editedItem[prop];
                            } else if (prop === 'longitude') {
                                body.point[1] = this.editedItem[prop];
                            } else {
                                body[prop] = this.editedItem[prop];
                            }
                        }
                    } else {
                        body = {...this.editedItem}
                    }
                    fetch(this.addUrl, {
                        method: 'POST',
                        body: JSON.stringify(body)
                    })
                        .then(res => res.json())
                        .then(json => {
                            this.garbage.push({...this.editedItem, id: json.id});
                            this.text = json.message;
                            this.color = json.success ? 'success' : 'error';
                            this.snackbar = true;
                            this.close();
                        })
                    }
                }
            }
    }
</script>

<style>
    .img-url {
        word-break: break-word;
    }
</style>
