<template>
    <v-container>
        <v-row v-if="!loading">
            <v-col cols="12" sm="6" v-for="g in garbage" :key="g.id">
                <router-link :to="'/garbage/' + g.id">
                    <div class="garbage-type d-flex justify-center align-center"
                         :style="{backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(' + g.img + ')'}">
                        <h1>{{ g.name }}</h1>
                    </div>
                </router-link>
            </v-col>
        </v-row>
        <div class="text-center" v-else>
            <v-progress-circular
                    :size="70"
                    :width="7"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </div>
    </v-container>
</template>

<script>
    export default {
        data() {
            return {
                garbage: [],
                loading: false
            }
        },
        mounted() {
            this.loading = true;
            fetch('https://save-nature-uzh.000webhostapp.com/garbage?props=name,img')
                .then(response => response.json())
                .then(json => {
                    this.loading = false
                    this.garbage = json
                })
        }
    }
</script>

<style scoped>
    .garbage-type {
        height: 300px;
        border-radius: 4px;
        color: #fff;
    }
</style>
