import Vue from 'vue'
import Router from 'vue-router'
import Garbage from "../components/Garbage";
import GarbageList from "../components/GarbageList";
import GarbageMap from "../components/GarbageMap";
import AddGarbageCollectionPoint from "../components/AddGarbageCollectionPoint";
import Login from "../pages/Login";
import Admin from "../pages/Admin";
import store from '../store';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '',
            component: GarbageList
        },
        {
            path: '/garbage/:id',
            component: Garbage
        },
        {
            path: '/garbage/:id/points',
            component: GarbageMap
        },
        {
            path: '/point/add',
            component: AddGarbageCollectionPoint
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/admin',
            name: 'admin_garbage',
            component: Admin,
            beforeEnter(to, from, next) {
                if (store.state.isLogged) {
                    next()
                } else {
                    next('/login')
                }
            }
        },
        {
            path: '/admin/garbage/:id/points',
            name: 'admin_garbage_points',
            component: Admin,
            beforeEnter(to, from, next) {
                if (store.state.isLogged) {
                    next()
                } else {
                    next('/login')
                }
            }
        }
    ],
    mode: 'history'
});
