<template>
    <div>
        <v-card max-width="700" class="mx-auto pa-5 my-5">
            <v-form
                    ref="form"
                    v-model="valid"
            >
                <v-text-field
                        v-model="name"
                        label="Назва точки сортування"
                ></v-text-field>
                <v-select
                        class="mt-5"
                        v-model="garbage"
                        :items="items"
                        item-text="name"
                        item-value="id"
                        multiple
                        label="Cміття"
                        :rules="[v => !!v || 'Будь ласка виберіть тип сміття']"
                        required
                ></v-select>
                <div id="map" ref="map" style="width: 90%; height:  500px; z-index: 0" class="mt-3 mx-auto"></div>
                <div class="text-center mt-4">
                    <v-btn color="success" :disabled="!valid || !point" @click="submitPoint" :loading="loading">Додати точку</v-btn>
                </div>
            </v-form>
        </v-card>
        <v-snackbar
                v-model="snackbar"
                :color="color"

        >
            {{ text }}
            <v-btn
                    text
                    @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>
    </div>

</template>

<script>
    export default {
        data() {
            return {
                map: null,
                valid: false,
                name: '',
                address: '',
                garbage: '',
                point: null,
                items: [],
                loading: false,
                snackbar: false,
                text: '',
                color: ''
            }
        },
        methods: {
            submitPoint() {
                const body = JSON.stringify({
                    name: this.name,
                    garbage: this.garbage,
                    point: [this.point.getPosition().lat(), this.point.getPosition().lng()],
                    address: this.address
                });

                console.log(body);

                this.loading = true;
                fetch('https://save-nature-uzh.000webhostapp.com/add', {
                    method: 'POST',
                    body,
                    headers: {
                        'Content-Type': 'application/json'
                    }
                })
                .then(response => response.json())
                .then(json => {
                    this.loading = false;
                    this.$refs.form.reset();
                    this.point.setMap(null);
                    this.point = null;
                    this.address = '';
                    this.text = json.message;
                    this.color = json.success ? 'success' : 'error';
                    this.snackbar = true;
                });
            }
        },
        mounted() {
           const map = new window.google.maps.Map(this.$refs.map, {
                center: {lat: 48.6213408, lng: 22.2973241},
                zoom: 13,
               styles: [
                   {
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#f5f5f5"
                           }
                       ]
                   },
                   {
                       "elementType": "labels.icon",
                       "stylers": [
                           {
                               "visibility": "off"
                           }
                       ]
                   },
                   {
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#616161"
                           }
                       ]
                   },
                   {
                       "elementType": "labels.text.stroke",
                       "stylers": [
                           {
                               "color": "#f5f5f5"
                           }
                       ]
                   },
                   {
                       "featureType": "administrative.land_parcel",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#bdbdbd"
                           }
                       ]
                   },
                   {
                       "featureType": "poi",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#eeeeee"
                           }
                       ]
                   },
                   {
                       "featureType": "poi",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#757575"
                           }
                       ]
                   },
                   {
                       "featureType": "poi.business",
                       "stylers": [
                           {
                               "visibility": "on"
                           }
                       ]
                   },
                   {
                       "featureType": "poi.park",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#e5e5e5"
                           }
                       ]
                   },
                   {
                       "featureType": "poi.park",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#9e9e9e"
                           }
                       ]
                   },
                   {
                       "featureType": "road",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#ffffff"
                           }
                       ]
                   },
                   {
                       "featureType": "road.arterial",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#757575"
                           }
                       ]
                   },
                   {
                       "featureType": "road.highway",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#dadada"
                           }
                       ]
                   },
                   {
                       "featureType": "road.highway",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#616161"
                           }
                       ]
                   },
                   {
                       "featureType": "road.local",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#9e9e9e"
                           }
                       ]
                   },
                   {
                       "featureType": "transit.line",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#e5e5e5"
                           }
                       ]
                   },
                   {
                       "featureType": "transit.station",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#eeeeee"
                           }
                       ]
                   },
                   {
                       "featureType": "water",
                       "elementType": "geometry",
                       "stylers": [
                           {
                               "color": "#c9c9c9"
                           }
                       ]
                   },
                   {
                       "featureType": "water",
                       "elementType": "labels.text.fill",
                       "stylers": [
                           {
                               "color": "#9e9e9e"
                           }
                       ]
                   }
               ]
            });

            const placeMarkerAndPanTo = (latLng, map) => {
                if (this.point) {
                    this.point.setPosition(latLng)
                } else {
                    this.point = new window.google.maps.Marker({
                        position: latLng,
                        map: map
                    });
                }
                map.panTo(latLng);
            };

            const getAddressByCoordinates = (latLng) => {
                new window.google.maps.Geocoder().geocode({
                    location: latLng,
                }, (results, status) => {
                        if (status === 'OK') {
                            if (results[0]) {
                                this.address = results[0].address_components[1].long_name + ', ' + results[0].address_components[0].long_name;
                            }
                        }
                })
            };

           map.addListener('click', (e) => {
               placeMarkerAndPanTo(e.latLng, map);
               getAddressByCoordinates(e.latLng);
           });


            fetch('https://save-nature-uzh.000webhostapp.com/garbage?props=name')
                .then(response => response.json())
                .then(json => this.items = json)
        }
    }
</script>

