<template>
    <v-container>
        <v-card v-if="!loading">
            <v-card-title>
                Як правильно сортувати {{ name.toLowerCase() }}?
            </v-card-title>
            <v-card-text v-html="description">
            </v-card-text>
        </v-card>
        <div class="text-center" v-else>
            <v-progress-circular
                    :size="70"
                    :width="7"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </div>

        <div class="text-center mt-5">
            <router-link :to="'/garbage/' + this.$route.params.id + '/points'">
                <v-btn rounded color="primary" dark>Знайти точки сортування</v-btn>
            </router-link>
        </div>
    </v-container>
</template>

<script>
    export default {
        data() {
            return {
                name: '',
                description: '',
                loading: false
            }
        },
        mounted() {
            this.loading = true;
            fetch('https://save-nature-uzh.000webhostapp.com/garbage/' + this.$route.params.id)
                .then(response => response.json())
                .then(json => {
                    this.name = json.name;
                    this.description = json.description;
                    this.loading = false
                })
        }
    }
</script>

