<template>
    <v-container
            class="fill-height"
            fluid
    >
        <v-row
                align="center"
                justify="center"
        >
            <v-col
                    cols="12"
                    sm="8"
                    md="4"
            >
                <v-card class="elevation-12">
                    <v-toolbar
                            color="primary"
                            dark
                            flat
                    >
                        <v-toolbar-title>Login form</v-toolbar-title>
                    </v-toolbar>
                    <v-card-text>
                        <v-form ref="form" v-model="valid">
                            <v-text-field
                                    v-model="username"
                                    label="Login"
                                    name="login"
                                    prepend-icon="mdi-account"
                                    type="text"
                                    :rules="[v => !!v || 'Username is required']"
                            />

                            <v-text-field
                                    v-model="password"
                                    label="Password"
                                    name="password"
                                    prepend-icon="mdi-lock"
                                    type="password"
                                    :rules="[v => !!v || 'Password is required']"
                            />
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer />
                        <v-btn color="primary" @click="login" :disabled="!valid" :loading="loading">Login</v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
        <v-snackbar
                v-model="snackbar"
                color="error"
        >
            {{ text }}
            <v-btn
                    text
                    @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    export default {
        data() {
            return {
                valid: true,
                username: '',
                password: '',
                snackbar: false,
                text: '',
                loading: false
            }
        },
        methods: {
            login() {
                const body = JSON.stringify({
                    username: this.username,
                    password: this.password
                });
                this.loading = true;
                fetch('https://save-nature-uzh.000webhostapp.com/login', {
                    method: 'POST',
                    body
                })
                .then(res => res.json())
                .then(json => {
                    if (json.success) {
                        this.loading = false;
                        this.$store.state.isLogged = true;
                        this.$router.go(-1);
                    } else {
                        this.loading = false;
                        this.text = json.message;
                        this.snackbar = true;
                        this.$refs.form.reset();
                    }
                })
            }
        }
    }
</script>

